<template>
  <div class="container">
    <el-card>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>个人管理</el-breadcrumb-item>
        <el-breadcrumb-item>信息列表</el-breadcrumb-item>
      </el-breadcrumb>
    </el-card>
    <el-card style="margin-top: 15px">
      <div class="Personal_user" v-if="Ative">
        <div class="head-portrait">
          <img :src="Logo" alt="logo" />
        </div>
        <div class="User_Text">
          <div class="Text_list">
            <div>
              用户名：<span>{{ UserText.username }}</span>
            </div>
            <div>
              <div class="key_text">
                <div class="intr">
                  <span>个人介绍：</span>
                  <span class="intr_text" v-if="!serverinp">{{
                    UserText.text
                  }}</span>
                </div>
                <div
                  class="inp"
                  v-if="serverinp"
                  style="flex: 1; margin: 0px 10px"
                >
                  <el-input
                    type="textarea"
                    v-model.trim="UserText.text"
                    ref="Focus"
                    maxlength="50"
                    show-word-limit
                  ></el-input>
                </div>
                <el-button type="primary" v-if="butStatus" @click="Editor('cb')"
                  >编辑</el-button
                >
                <el-button type="primary" v-else @click="Editor('ok')"
                  >完成</el-button
                >
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="ative" v-else>
        <img :src="url" alt="error" />
        <h1>未登录</h1>
      </div>
    </el-card>
  </div>
</template>
<script>
export default {
  name: 'Personal',
  data() {
    return {
      Logo: require('../../assets/images/logo_1.jpg'),
      url: require('../../assets/images/Sign.gif'),
      UserText: {},
      butStatus: true,
      serverinp: false,
      Ative:'',
    }
  },
  created() {
    this.GetKeyUser()
    this.Ative = this.Storage.Session.get('token')
  },
  methods: {
    GetKeyUser() {
      const id = JSON.parse(this.Storage.Session.get('data')).id
      this.$http.get(this.$api.API_SERVER_KEYUSER + id).then((res) => {
        if (res.data.status != 200) {
          return this.$message.error('获取数据失败')
        }
        this.UserText = res.data.data
      })
    },
    Editor(val) {
      if (val == 'ok') {
        this.serverinp = !this.serverinp
        this.butStatus = !this.butStatus
        const params = {
          id: this.UserText.id,
          text: this.UserText.text,
        }
        this.$http.put(this.$api.API_SERVER_PUT_Text, params).then((res) => {
          if(res.data.status!==201){
            return this.$message.error(res.data.msg)
          }
          this.$message.success(res.data.msg)
        })
      } else {
        this.serverinp = !this.serverinp
        this.butStatus = !this.butStatus
        // this.$nextTick((_) => {
        //   this.$refs.Focus.$refs.input.focus()
        // })
      }
    },
  },
  mounted() {
    // const that = this
    // $('.but').on('click', function () {
    //   that.inputValue = $('.intr_text').text()
    //   that.butStatus=!that.butStatus
    //   $('.inp').removeClass('hide')
    //   $('.intr_text').addClass('hide')
    //   $('#serverinp').focus()
    // })
  },
}
</script>
<style lang="less" scoped>
.Personal_user {
  display: flex;
}
.head-portrait {
  width: 200px;
  height: 200px;
  background-color: #ccc;
  border-radius: 50%;
  overflow: hidden;
  img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
}
.User_Text {
  position: relative;
  box-sizing: border-box;
  margin-left: 20px;
  .Text_list {
    position: absolute;
    bottom: 20px;
    width: 600px;
    font-size: 14px;
    > div {
      width: 100%;
      //   height: 40px;
      //   line-height: 40px;
      padding: 5px 0px;
      word-wrap: break-word;
      word-break: break-all;
      border-bottom: 1px solid #ccc;
    }
  }
}
.key_text {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.el-button {
  padding: 8px 10px;
}
.el-input {
  width: 100%;
}
.hide {
  display: none;
}
.ative{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
</style>